			level: 0,
			durationAnn: 0,
			Notifyid: 0,
			grade: 0,
			ammoClip: 0,
			ammoRemain: 0,
			money: 0,
			kmh:0,
			count:0,
			duration: 0,
			progress: 0,
			interval: 0,
			notiInterval: 0,
			Helpdura: 0,
			timeout: 0,
			isOpen: false,
			isSpeedo: false,
			isAnnounce: false,
			isLoadingbar: false,
			holdingWeapon: false,
			isHelpNotify: false,
			isGreenZone: false,
			doorsactivated: false,	
			lightsactivated: false,	
			engineactivated: false,
			list: [
			// 	{
			// 	title: "test",
			// 	message: "test nachricht du fetter hurensohn das ist ein test. Zum testen dieser nachricht fertig aus ende",
			// 	img: "info.png",
			// 	color : "rgba(238, 16, 16, 0.15)",
            // bgcolor : "linear-gradient(90deg, #330000 0%, rgba(51, 0, 0, 0) 100%)",
			// }
				],
		}
  },
  created() {
		let that = this
		setInterval(that.getNow, 1000);
		setInterval(function() {
		if(that.Helpdura > 0) {
			that.Helpdura -= 500
		}
	}, 500)
  },
  unmounted() {
    window.removeEventListener('message', this.listener);
  },
  mounted() {
    this.listener = (event) => {
		var e = event.data;

		if ("open" == e.action) {
			this.isOpen = true;        
		} else if ("close" == e.action) {
			this.isOpen = false;
		} else if ("update" == e.action) {
			this.location = e.location;
			this.id = e.id;
			this.jobLabel = e.job;
			this.grade = e.grade;
		} else if ("setMoney" == e.action) {
			this.money = e.money;
		} else if ("announce" == e.action) {
			//console.log(e.time)
			this.setGlobalNotification(e.text, e.time)
		} else if ("wArmed" == e.action) {
			if (e.type == "waffe") {
			this.holdingWeapon = true;
			this.ammoClip = e.ammoClip;
			this.ammoRemain = e.ammoRemaining;
			} else {
			this.holdingWeapon = false;
			}
		} else if ("loadingbar" == e.action) {
			this.setProgressbar(e.loadingTime);
			this.loadingbarMessage = e.text
		}else if ("setVoiceLevel" == e.action) {
			this.level = e.level;
			// //console.log(this.level)
		} else if ("greenzone" == e.action) {
			this.isGreenZone = e.data
		} else if ("helpnotify" == e.action) {
			this.isHelpNotify = true
			// console.log("helpnotify" ,this.isHelpNotify)
			this.Helpdura = 1000
			this.helptext = e.text
		} else if ("notify" == e.action) {
			this.pushPlayerNotification(e.message, e.duration, e.title, e.img, e.color, e.bgcolor, e.notivalueDuration)
		} else if ("vehicle" == e.action) {
			if (e.type == "inside") {
				this.isSpeedo = true;
				this.kmh = e.speedLevel;
				this.fuel(e.fuelLevel);
				this.speedo(this.kmh);
			} else if (e.type == "engine") {
				this.engineactivated = e.isOn;
			} else if (e.type == "lights") {
				this.lightsactivated = e.isOn;
			} else if (e.type =="doors") {
				this.doorsactivated = e.isOn;
			} else {
				this.isSpeedo = false;
			}
		} else if ("water" == e.action) {
			this.waterint = e.data
		} else if ("food" == e.action) {
			this.foodint = e.data
		}
    }

    window.addEventListener('message', this.listener);
  },
  methods: {
		getNow: function() {
			const today = new Date();
			const date = today.getDate()+'.'+(today.getMonth()+1)+'.'+today.getFullYear();
			
			if (today.getHours() < 10) {
				this.time = "0" + today.getHours() + ":" + today.getMinutes();
			} else if (today.getMinutes() < 10 ) {
				this.time = today.getHours() + ":" + "0" + today.getMinutes();
			} else {
				this.time = today.getHours() + ":" + today.getMinutes();
			}

			this.date = date;
		},
		removeGlobal: function () {
			this.isAnnounce = false;
		},
		setGlobalNotification: function (message, duration) {
			this.annmessage = message
			this.durationAnn = duration
			this.isAnnounce = true;
			this.timeoutAnn = setTimeout(this.removeGlobal, this.durationAnn)

		},
		removeN(notify) {
			const index = this.list.indexOf(notify)
			//console.log(index, JSON.stringify(notify))
			this.list.splice(index, 1)
		},
		pushPlayerNotification(message, duration, title = "", img="", color="",  bgcolor="", notivalueDuration="") {
			const notify = { message, notivalueDuration, title, color, img, bgcolor, notyID: this.Notifyid++ }
			this.list.push(notify)
			setTimeout(this.removeN.bind(null, notify), duration)
		},
		speedo(percent) {
			const speedProgress = document.getElementById('circle-speed-progress');
			const MaxSpeed = 300;

			let speedRation = MaxSpeed / percent;
			let speedOffset = 100 / speedRation;

			let offsetSpeed = 196.3495 + (589.0486 - speedOffset / 100 * 589.0486);
			if (offsetSpeed < 785.3981) {
				speedProgress.style.strokeDashoffset = offsetSpeed;

				if(offsetSpeed < 196.3495) 
					speedProgress.style.strokeDashoffset = 196.3495;
			} else 
				speedProgress.style.strokeDashoffset = 785.3981;
		},
		fuel(percent) {
			const FuelProgress = document.getElementById('circle-fuel-progress');

			let offsetFuel = 654.4722 + (130.9259 - percent / 100 * 130.9259);

			if (offsetFuel < 785.3981) {
				FuelProgress.style.strokeDashoffset = offsetFuel;

				if(offsetFuel < 654.4722) 
					FuelProgress.style.strokeDashoffset = 654.4722;
			} else 
				FuelProgress.style.strokeDashoffset = 785.3981;
		},
		formatNumber(num) {
			return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.')
		},
		remove: function () {
			this.isLoadingbar = false
			clearInterval(this.interval)
			this.interval = 0
			this.progress = 0
		},
		upProgress: function () {
			var multiplier = this.duration / 1000
			this.progress = this.progress + (100 / multiplier)
			if (this.progress > 100) {
				this.progress = 0
				this.isLoadingbar = false
				clearInterval(this.interval)
				this.interval = 0
			}
		},
		setProgressbar: function (duration) {
			this.isLoadingbar = true
			this.progress = 0
			this.interval = 0
			this.timeout = 0
			this.duration = duration
			this.interval = setInterval(this.upProgress, 1000);
		}
	}
}
</script>

